<template>
  <div class="comic">
    <main class="main" role="main">
        <div class="container">
          <div class="row">
            <div v-for="data in comic_content">
              <div class="col-xs-12 col-sm-3">
                <div class="post-featured-image">
                  <router-link :to="{name:'ComicName', query:{name:data.name} }" class="thumbnail"><img :src="data.path" width="700" height="438" :alt="data.name">{{ data.name }}</router-link>
                </div>
              </div>
            </div>
          </div>
        </div>
    </main>
  </div>
</template>

<script>
export default {
  name: 'comic',
  components: {
  },
  data() {
    return{
      comic_content : [],
    }
    
  },

  mounted() {
  this.$ajax.get('http://127.0.0.1:8000/api/show_books').then(response => {
      for (var i=0; i < response.data.list.length; i++) {
        var path = "/static/" + response.data.list[i].fields.book_path + "/" + response.data.list[i].fields.book_name
        var comic_content = { "path": path, "name": response.data.list[i].fields.book_path }; // 字典 
        this.comic_content.push(comic_content)      
      }
  }, response => {
    console.log(response);
  })
  }
}
</script>



