<template>
  <div class="ComicName">
    <h1>{{name}}</h1>
  </div>
</template>

<script>
export default {
  	name: 'Haizeiwang',
    data () {
      return {
        name: ''
      }
    },
	mounted(){

	    this.name = this.$route.query.name;

	 }
}


</script>


